.profile {
	.topImage {
		box-shadow: 5px 5px 15px rgba(0, 0, 0, 1);
		img {
			width: 100%;
			margin-bottom: -40%;
		}
	}
	.contentBlockWrapper {
		position: relative;
		background: rgb(72, 2, 78);
		.contentBlock {
			display: flex;
			flex-direction: column;
			position: relative;
			.pageWrap {
				display: flex;
				background: mediumslateblue;
				padding: 0 30px 30px;
				box-shadow: 5px 5px 15px rgba(0, 0, 0, 1);
				width: 100%;
				flex-wrap: nowrap;
				position: relative;
				@media screen and (max-width: 650px) {
					flex-wrap: wrap;
				}
				.avatar {
					margin: -37px 30px 0 0;
					text-align: center;
					img {
						height: 150px;
						border: 2px solid #000;
						box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.8);
					}
					input {
						text-align: center;
						display: block;
						width: calc(100% - 4px);
						height: 20px;
						margin: -20px 2px 0;
						position: relative;
					}
				}
				.pageInfoWrap {
					margin: 20px 0 0;
					font-family: sans-serif;
					width: 100%;
					.pageName {
						font-size: 25px;
						border-bottom: 1px solid #000;
						padding-bottom: 10px;
						padding-left: 20px;
					}
					.status {
						margin-top: 5px;
						height: 15px;
						.myStatus {
							border: 1px dashed transparent;
							display: block;
							max-width: 200px;
							&:hover {
								border: 1px dashed #000;
								cursor: pointer;
							}
						}
						.enterStatus {
							border: 1px dashed transparent;
							color: #444;
							display: block;
							max-width: 200px;
							&:hover {
								border: 1px dashed #000;
								cursor: pointer;
							}
						}
					}

					.personalInfoWrap {
						.topBorder {
							margin-top: 10px;
							position: relative;
							line-height: 17px;
							background: rgb(106, 81, 194);
							padding: 0 10px;
							.editModeButton {
								height: 100%;
								color: #eee;
								position: absolute;
								right: 3px;
								background: transparent;
								padding: 0 5px;
								display: none;
								@media (pointer: coarse) {
									display: inline;
								}
								&:hover {
									color: #bbb;
								}
							}
						}
						.personalInfo {
							display: flex;
							justify-content: space-around;
							flex-wrap: nowrap;
							@media screen and (max-width: 800px) {
								flex-wrap: wrap;
							}
							.aboutMeBlock {
								margin: 0 10px 0 15px;
								min-width: 100px;
								max-width: 100%;
								.pageInfoRow {
									margin: 7px 0;
									span {
										font-weight: 700;
									}
								}
							}
							.contactsBlock {
								margin: 0 10px 0 15px;
								.contactsTitle {
									font-size: 17px;
									margin: 7px 0;
								}
								.contactsItem {
									margin: 5px 0 0 5px;
									span {
										text-transform: capitalize;
										font-weight: 700;
									}
								}
							}
						}
						&:hover {
							.topBorder {
								.editModeButton {
									display: inline;
								}
							}
						}
					}
				}
			}
			.postsBlock {
				margin-top: 35px;
				.newPostBlock {
					padding: 20px 20px 40px;
					background: mediumslateblue;
					box-shadow: 5px 5px 15px rgba(0, 0, 0, 1);
					input {
						font-size: 17px;
						width: 100%;
						padding-left: 10px;
						line-height: 35px;
					}
					.buttonWrap {
						margin-top: 10px;
						position: relative;
						width: 100%;
						button {
							margin-right: 10px;
							position: absolute;
							right: 0;
							font-size: 15px;
							line-height: 24px;
							padding: 0 15px;
							border-radius: 4px;
							background: navy;
							color: #ddd;
							box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.9);
							&:hover {
								background: mediumblue;
							}
							&:active {
								box-shadow: 1px 1px 5px rgba(0, 0, 0, 1);
								right: -2px;
								top: 2px;
								background: darkgreen;
							}
						}
					}
				}
				.existingPostsBlock {
					margin-bottom: 50px;
					.post {
						margin-top: 20px;
						background: mediumslateblue;
						padding: 10px;
						font-size: 18px;
						box-shadow: 5px 5px 15px rgba(0, 0, 0, 1);
						.likes {
							text-align: right;
							margin-top: 10px;
						}
					}
				}
			}
		}
	}
}
